:root {
    --deep-purple: #482880;
    --purple: #673ab7;
    --light-purple: #8561c5;
    --deep-cyan: #008394;
    --cyan: #00bcd4;
    --light-cyan: #33c9dc;
    --deep-dark: #121212;
    --dark: #1e1e1e;
    --light-dark: #363636;
}
.purple {
    background-color: var(--purple);
}
.deep-purple {
    background-color: var(--deep-purple);
}
.light-purple {
    background-color: var(--light-purple);
}
.cyan {
    background-color: var(--cyan);
}
.deep-cyan {
    background-color: var(--deep-cyan);
}
.light-cyan {
    background-color: var(--light-cyan);
}
.deep-dark {
    background-color: var(--deep-dark);
}
.dark {
    background-color: var(--dark);
}
.light-dark {
    background-color: var(--light-dark);
}
.purple-font {
    color: var(--purple);
}
.deep-purple-font {
    color: var(--deep-purple);
}
.light-purple-font {
    color: var(--light-purple);
}
.cyan-font {
    color: var(--cyan);
}
.deep-cyan-font {
    color: var(--deep-cyan);
}
.light-cyan-font {
    color: var(--light-cyan);
}
.dark-font {
    color: var(--dark);
}
.light-dark-font {
    color: var(--light-dark);
}
.green-font {
    color: #89d71d;
}
.green-font {
    color: #89d71d;
}
.left {
    text-align: left;
}
.center {
    text-align: center;
}
body {
    margin: 0;
    font-family: "Poppins", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
nav {
    transition: all 0.35s;
    width: 100%;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 10;
}
.nav-max {
    background-color: var(--purple);
    padding: 15px 0px;
    box-shadow: 0 0 1rem 0.2rem #7600ca;
}
.nav-min {
    background-color: var(--deep-purple);
    padding: 5px 0px;
}
nav ul {
    list-style: none;
    margin: 0px 20px;
    padding: 0;
}
nav ul li {
    display: inline;
    margin: 0;
    padding: 0;
}
nav .link {
    font-size: calc(1rem + 0.5vmin);
    text-transform: uppercase;
    color: white;
    margin: 20px;
}
nav .badge {
    margin: 0px 5px;
    color: white;
}
nav a:hover {
    border-bottom: 2px solid var(--light-purple);
}
.active {
    border-bottom: 2px solid var(--deep-cyan);
}
a {
    text-decoration: none;
}
p {
    margin: 5px 0px;
    padding: 0;
}
.app-header {
    min-height: 60vh;
    background-image: url("../images/header-background.jpg");
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}
.app-header div {
    text-align: left;
    margin-left: 10vw;
}
.background-tint {
    background-blend-mode: multiply;
    transition: all 0.3s;
}
.light-tint {
    background-color: rgb(112, 132, 177);
}
.dark-tint {
    background-color: rgb(17, 25, 54);
}
.content {
    min-height: 60vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: white;
    padding: 50px 0px;
    font-size: calc(8px + 1vmin);
}
.general {
    margin: 20px;
}
.general img {
    width: calc(50px + 20vmin);
    height: calc(50px + 20vmin);
    border-radius: 50%;
    object-fit: cover;
    transition: all 0.3s;
}
.general img:hover {
    width: calc(150px + 20vmin);
    height: calc(150px + 20vmin);
}
.flex-column {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.quote {
    font-size: 0.9em;
    font-style: italic;
    padding-top: 1.5em;
}
.grid-container {
    display: grid;
    grid-gap: 5rem;
    grid-template-columns: auto auto auto;
    padding: 1rem;
}
.grid-item {
    max-width: 15rem;
}
.image-grid {
    display: grid;
    grid-gap: 0.4rem;
    grid-template-columns: auto auto auto;
    padding: 10px;
}
.grid-image {
    --max: 25rem;
    --min: 11rem;
    --avg: 30vw;
    width: var(--avg);
    height: var(--avg);
    max-width: var(--max);
    max-height: var(--max);
    min-width: var(--min);
    min-height: var(--min);
    display: block;
    object-fit: cover;
    transition: all 0.5s;
}
.grid-image-container {
    position: relative;
}
.grid-image-container:hover .overlay {
    opacity: 0.9;
}
.grid-image-container:hover .overlay-text {
    top: 50%;
}
.overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: 0.5s ease;
}
.overlay-text {
    color: white;
    position: absolute;
    top: -50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
    transition: 0.5s ease;
}
@media screen and (max-width: 600px) {
    nav .badge {
        visibility: hidden;
        position: absolute;
    }
    .grid-container {
        grid-gap: 50px;
    }
    .content {
        flex-direction: column;
    }
    .image-grid {
        grid-template-columns: auto auto;
    }
}
